	module.exports = {"data":{"markdownRemark":{"html":"<p>Illustration of the protagonist of the <a href=\"/cats_story\">cat</a> , \"Me\". At that time I liked to stretch the characters side-by-side.</p>\n<p>At the time I was planning to make CATS STORY RPG, I was planning to create a story from 1P gag cartoon of world view. At that time all heads were cats' cats, and the scenario was also a dynamic king dynasty poetry.</p>\n<p>It will be a dark scenario depicting a confrontation with human and cat human beings (which became a slightly subtle description rather than a beast person), but that is about a year and a half later.</p>","frontmatter":{"cover":null,"date":"1995-01-09T10:00:24+09:00","strDate":"1995/1/9","title":"Cat fight"}}},"pathContext":{"path":"/en/blog/1995/01/09-cats-story/","langKey":"en"}}

